@import url('https://fonts.googleapis.com/css2?family=Lora&display=swap');
@import './reset.css';

:root {
  --clay: #995F49;
  --stone: #F5EFED;
  --sand: #CCAFA4;
  --graphite: #242723;
  --base-unit: 4px;
}

body {
  font-family: 'Lora', serif;
  font-weight: 400;
  font-style: normal;
  font-size: calc(var(--base-unit) * 4);
  letter-spacing: .035em;
  line-height: 1.7em;
  color: var(--graphite);
  background-color: var(--stone);
}

h1,
h2,
h3 {
  font-family: 'Lora', serif;
  letter-spacing: 0em;
  text-transform: none;
}

p {
  max-width: 70ch;
}

a {
  color: var(--clay);
}

img {
  width: 100%;
}

header {
  padding: calc(var(--base-unit) * 8);
  margin-bottom: calc(var(--base-unit) * 5);
  text-align: center;
}

header h1 {
  max-width: 1300px;
  margin: auto;
}

/* Utility Classes */
.visuallyHidden {
  display: none;
}