.Gallery_wrapper__1wNZC {
  display: grid;
  grid-template-columns: calc(var(--base-unit) * 50) 1fr;
  grid-gap: calc(var(--base-unit) * 7);
  gap: calc(var(--base-unit) * 7);
  margin-top: calc(var(--base-unit) * 20);
}

@media screen and (max-width: 600px) {
  .Gallery_wrapper__1wNZC {
    display: block;
  }
}

/* Cards */  
.Gallery_card__dsvkt {
  padding: calc(var(--base-unit) * 3);
  text-align: center;
  position: relative;
  border: 1px solid var(--clay);
  outline: 2px solid transparent;
  outline-offset: 1px;
  transition: outline-color 0.2s ease;
  background-color: white;
}
  
.Gallery_card__dsvkt:focus,
.Gallery_card__dsvkt:hover {
  outline-color: var(--clay);
}
  
.Gallery_card__dsvkt:hover img {
  transition: opacity 0.2s ease-in-out;
}
  
.Gallery_card__dsvkt:hover img {
  opacity: .8;
}
  
.Gallery_link__2TcH3 {
  text-decoration: none;
}

.Gallery_designer__3HPrk {
  font-size: 13px;
  color: var(--graphite);
}

